<template lang='pug'>
cube-form.form-register(
  :model='model'
  :schema='schema'
  :immediate-validate='false'
  :options='options'
  @submit='submitHandler'
  @reset='resetHandler'
  @validate='validateHandler')
</template>

<script>
/**
* @name 
 */
export default {
  name: 'FormLogin',
  data () {
    return {
      // form
      validity: {},
      valid: undefined,
      model: {}, // 
      schema: { // 
        fields: [
          {
            modelKey: 'username',
            type: 'input',
            label: '',
            props: {
              placeholder: ''
            },
            rules: {
              required: true
            }
          },
          {
            modelKey: 'password',
            type: 'input',
            label: '',
            props: {
              type: 'password',
              placeholder: '',
              eye: { open: false, reverse: false }
            },
            rules: {
              required: true
            }
          },
          {
            modelKey: 'password2',
            type: 'input',
            label: '',
            props: {
              type: 'password',
              placeholder: '',
              eye: { open: false, reverse: false }
            },
            rules: {
              required: true
            }
          },
          {
            modelKey: 'tell',
            type: 'input',
            label: '',
            props: {
              placeholder: ''
            },
            rules: {
              required: true
            }
          },
          {
            type: 'submit',
            label: ''
          },
          {
            type: 'reset',
            label: ''
          }
        ]
      },
      options: { // 
        scrollToInvalidField: true,
        layout: 'standard' // classic fresh
      }
    }
  },
  methods: {
    validateHandler (result) {
      this.validity = result.validity
      this.valid = result.valid
      console.log('validity', result)
    },
    submitHandler (e) {
      e.preventDefault()
      console.log('')
      this.$post({
        url: '/userinfo/register',
        data: {
          username: this.model.username,
          password: this.model.password,
          tell: this.model.tell,
          type: 'user'
        }
      }).then(data => {
        if (!data) return
        this.$store.dispatch('recordUser', data)
        this.$router.replace('/profile')
      })
    },
    resetHandler (e) {
      console.log('', e)
    }
  }
}
</script>

<style lang='sass' scoped>

</style>



