<template lang='pug'>
.order-wrap
  PageHeader.page_header(title='')
  .order-content
    .order-address(
      v-if="address"
      @click="selectAddress")
      .address-title 
      .address-info {{address.address}}
      .user-info
        span.user-name {{address.name}}
        span.user-phone {{address.tell}}
      i.iconfont.icon-right
    .order-detail
      .shop-name {{shopInfo.name}}
      .cart-list
        .cart-item(v-for="item in cartList")
          img.goods-img(src="item.imgsrc")
          .goods-name {{item.name}}
          .goods-quantity x {{item.quantity}}
          .goods-price {{item.price}}
      .delivery
        .delivery-name 
        .delivery-price {{shopInfo.delivery_price}}
      .total
        span.total-name 
        span.total-price {{totalPrice}}
  .page_bottom(@click="submitOrder") 
</template>

<script>
/**
 * @name PlaceOrder
 */
import { showPopup } from '@/common/alert'
import {mapState} from 'vuex'
import PageHeader from '@/components/PageHeader'
export default {
  name: 'PlaceOrder',
  components: {
    PageHeader
  },
  computed: {
    ...mapState(['addresslist']),
    totalPrice () {
      const cartPrice = this.cartList.reduce((result, item) => {
        return result + item.price * item.quantity
      }, 0)
      const deliveryPrice = this.shopInfo.delivery_price || 0
      return cartPrice + deliveryPrice
    }
  },
  data () {
    let cartList = JSON.parse(window.localStorage.getItem('Cart_Map'))['shop' + this.$route.query.id]
    console.log('cartlist', cartList)
    return {
      shopId: this.$route.query.id || null,
      cartList: cartList || [],
      shopInfo: {},
      address: {}
    }
  },
  methods: {
    selectAddress () {

    },
    submitOrder () {
      if (!this.address.id && this.address.id !== 0) {
        showPopup('', 'warning')
        return
      }
      const date = this.$moment().format('YYYY-MM-DD HH:mm:ss')
      this.$post({
        url: `/orderinfo/insert`,
        data: {
          date,
          shopId: this.shopInfo.id,
          addressId: this.address.id,
          orderGoods: this.cartList.map(item => ({
            goodsId: item.id,
            quantity: item.quantity,
            price: item.price
          }))
        }
      }).then((res) => {
        if (res) {
          showPopup('', 'success')
          this.clearCart() // 
          setTimeout(() => {
            this.$router.push({
              path: '/order'
            })
          }, 1000)
          this.$store.dispatch('getUserinfo')
        }
      })
    },
    clearCart () {
      let cartList = JSON.parse(window.localStorage.getItem('Cart_Map')) || {}
      const key = 'shop' + this.shopId
      const newCartList = {
        ...cartList,
        [key]: []
      }
      window.localStorage.setItem('Cart_Map', JSON.stringify(newCartList))
    }
  },
  mounted () {
    // 
    this.address = (this.addresslist && this.addresslist.length) ? this.addresslist[0] : {}
    console.log('', this.addresslist, this.address)
    // 
    if (this.shopId || this.shopId === 0) {
      this.$get({
        url: `/shopInfo/shopid`,
        params: {
          shopid: this.shopId
        }
      }).then(res => {
        if (!res) return
        this.shopInfo = res
      })
    }
  }
}
</script>

<style lang='sass' scoped>
.order-wrap
  background: linear-gradient(#1abc9c 25%, #f5f5f5 50%)
  height: 100%
  .order-content
    height: calc(100% - 84px)
    overflow: auto
    padding: 4vw
    box-sizing: border-box
    position: relative
    .order-address
      width: 100%
      margin-bottom: 18px
      color: #fff
      box-sizing: border-box
      .address-title
        font-size: 4vw
        padding: 5px 0
        color: #c9f7e3
      .address-info
        font-size: 5vw
        font-weight: bold
        padding: 5px 0
      .user-info
        font-size: 4.2vw
        .user-name
          margin-right: 8px
      .icon-right
        position: absolute
        right: 10px
        top: 13vw
    .order-detail
      background: #fff
      width: 100%
      padding: 10px
      box-sizing: border-box
      color: #555
      .shop-name
        font-weight: bold
        padding: 5px
        border-bottom: 1px solid #ddd
      .cart-list
        .cart-item
          display: flex
          height: 10vw
          line-height: 10vw
          font-size: 3.2vw
          .goods-img
            width: 8vw
            height: 8vw
            padding: 2vw
            margin-right: 1px
          .goods-name,.goods-quantity,.goods-price
            flex: 1
            overflow: hidden
          .goods-name
            flex: 2
      .delivery
        border-bottom: 1px solid #ddd
        display: flex
        font-size: 3.2vw
        height: 10vw
        line-height: 10vw
        .delivery-name
          flex: 2
          margin-left: 13vw
        .delivery-price
          flex: 1
      .total
        padding: 5px
        text-align: right
        vertical-align: text-bottom
        padding-right: 50px
        .total-name
          font-size: 3.2vw
        .total-price
          font-size: 4vw
  .page_bottom
    height: 40px
    text-align: center
    line-height: 40px
    font-size: 4vw
    color: #1abc9c
    background: #fff
    box-shadow: 0px 0px 10px #dfdfdf
</style>



